.search {
  position: relative;

  &__form {
    display: flex;
  }

  &__btn {
    width: 25px;
    height: 25px;
    fill: $color-light;

    @include hover {
      fill: $color-orange;
    }

    &:active {
      fill: $color-grenadier;
    }

    @include small-mobile() {
      width: 20px;
      height: 20px;
    }
  }

  &__icon-search {
    width: inherit;
    height: inherit;
    fill: inherit;
    transition: fill .25s ease-in-out;
  }

  &__bar {
    position: absolute;
    z-index: 999;
    top: 0;
    right: 0;
    visibility: hidden;
    padding: 4px 0 1px 0;
    background-color: $color-mine;
    transform: translateY(-400%);
    transition: transform .3s ease-in-out, visibility .3s ease-in-out;

    &--active {
      visibility: visible;
      transform: translateY(0);
    }
  }

  &__input {
    margin-right: 9px;
    width: 308px;
    border-bottom: 1px solid $color-mercury;
    background-color: transparent;
    font-size: 16px;
    line-height: 20.64px;
    color: $color-mercury;
    transition: border-color .25s ease-in-out;

    &::placeholder {
      color: inherit;
    }

    &:focus,
    &:hover {
      outline: none;
      border-color: $color-orange;
    }

    &:active {
      border-color: $color-grenadier;
    }

    @include mobile() {
      width: 251px;
      font-size: 14px;
      line-height: 18.06px;
    }
  }

  &__close {
    width: 23px;
    height: 23px;

    @include mobile() {
      width: 18px;
      height: 18px;
    }
  }

  &__icon-close {
    width: 10.68px;
    height: 10.57px;

    @include mobile() {
      width: 8.36px;
      height: 8.27px;
    }
  }
}
