.menu {

  &__list {
    @include flex-all-center;

    @include mid-tablet {
      @include flex-all-sb;
      width: 100%;
    }

    @include big-mobile() {
      @include flex;
      flex-direction: column;
      align-items: start;
      justify-content: start;
      position: absolute;
      z-index: 5;
      top: calc(100% - 45px);
      left: -100%;
      visibility: hidden;
      padding: 48px 21px 60px;
      width: 100%;
      height: calc(var(--vh) - var(--header-menu-height));
      background-color: $color-mine;
      transition: visibility .2s ease-in-out, transform .2s ease-in-out;

      &--active {
        visibility: visible;
        transform: translateX(100%);
      }
    }
  }

  &__item {
    $itemBlockName: &;

    &--phone {
      display: none;


      @include big-mobile() {
        display: inline-block;
        order: 1;
        margin-top: auto;
      }
    }

    @include mr(79px);

    &#{ $itemBlockName }--last {
      margin-right: 0;
    }

    @include big-desktop {
      @include mr(55px);
      &#{ $itemBlockName }--last {
        margin-right: 0;
      }
    }

    @include tablet {
      @include mr(33px);
      &#{ $itemBlockName }--last {
        margin-right: 0;
      }
    }

    @include mid-tablet {
      @include mr(15px);
      &#{ $itemBlockName }--last {
        margin-right: 0;
      }
    }

    @include big-mobile() {
      @include mr(0px);
      @include mb(30px);
      &#{ $itemBlockName }--last {
        margin-bottom: 0;
      }
    }
  }

  &__link {
    position: relative;
    z-index: 2;
    background-color: transparent;
    font-family: 'FirsNeue' , sans-serif;
    font-size: 16px;
    line-height: 20.64px;
    color: $color-light;
    transition: color .25s ease-in-out;

    @include ghost(120%, 110%, $color-tuscany);

    &:focus {
      outline: none;
      &::after {
        opacity: 1;
      }
    }

    @include hover {
      &::after {
        opacity: 0;
      }
      color: $color-orange;
    }

    &:active {
      &::after {
        opacity: 0;
      }
      color: $color-grenadier;
    }
  }
}

.burger {
  @include burger;
  display: none;

  @include big-mobile() {
    display: inline-block;
  }
}
