/* stylelint-disable */

@mixin burger {
  --burger-width: 20px;
  --burger-height: 18px;
  --burger-line-height: 2px;

  position: relative;
  z-index: 99999;
  overflow: hidden;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: $color-light;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - var(--burger-line-height));
    width: 50%;
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &__close {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 99999;
    width: 18px;
    height: 18px;
    transform: translate(200%, -50%);
    transition: transform .4s ease-in-out;
  }

  &__close-icon {
    width: 10.68px;
    height: 10.57px;
  }

  &--active {
    &::before {
      transform: translateX(-200%);
      transition: transform 0.2s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: translateX(-200%);
      transition: transform 0.3s ease-in-out;
    }

    &::after {
      transform: translateX(-200%);
      transition: transform 0.4s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__close {
      transform: translate(-50%, -50%);
    }
  }
}
