.projects {
  margin-bottom: 69px;

  @include mobile {
    margin-bottom: 32px;
  }

  &__title {
    margin-bottom: 12px;
    color: $color-mine;

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: $grid-gap;
    grid-auto-flow: dense;

    @include tablet {
      grid-template-columns: repeat(8, 1fr);
      row-gap: 28px;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      row-gap: 27px;
    }

    @include mobile {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 0;
      row-gap: 25px;
    }
  }

  &__item {
    grid-column: span 2;

    @include tablet {
      grid-column: span 2;

      &:nth-child(3) {
        grid-column: span 4;
      }
    }

    @include small-tablet {
      grid-column: span 2;

      &:nth-child(3) {
        grid-column: span 2;
      }
    }

    @include mobile {
      grid-column: span 2;

      &:nth-child(3) {
        grid-column: span 2;
      }
    };

    &--large {
      grid-column: span 6;

      @include tablet {
        grid-column: span 5;

        &-second {
          grid-column: 4 / span 5;
        }
      }

      @include small-tablet {
        grid-column: span 6;

        &-second {
          grid-row: 4;
        }
      }

      @include mobile {
        grid-column: span 2;

        &-second {
          @include mobile {
            grid-row: 7;
          }
        }
      }
    }

    &--mid {
      grid-column: span 3;

      @include small-tablet {
        grid-column: span 3;
      }

      @include mobile {
        grid-column: span 2;
      }
    }
  }
}

.project {
  position: relative;
  @include flex;
  flex-direction: column;
  height: 100%;

  &--large {
    justify-items: flex-start;
    align-items: flex-start;
    padding-top: 21px;
    padding-left: 30px;
    padding-bottom: 29px;
    padding-right: 49%;
    @include bg-cover();

    @include big-desktop {
      padding-right: 20%;
    }

    @include tablet {
      padding-top: 27px;
      padding-right: 45%;
    }

    @include mid-tablet {
      padding-right: 20%;
    }

    @include small-tablet {
      padding-top: 28px;
      padding-bottom: 30px;
      padding-right: 54.3%;
    }

    @include big-mobile {
      padding-right: 20%;
    }

    @include mobile {
      padding-bottom: 32px;
      padding-right: 30px;
      height: 422px;
    }

    .project__date {
      margin-top: 0;
      margin-bottom: 2px;
      color: $color-light;

      @include mobile {
        margin-top: auto;
        margin-bottom: 7px;
      }
    }

    .project__title {
      margin-bottom: 6px;
      line-height: 100%;

      @include big-mobile {
        font-size: 24px;
      }

      @include mobile {
        margin-bottom: 13px;
      }
    }

    .project__descr {
      margin-bottom: 54px;
      color: $color-light;

      @include small-tablet {
        margin-bottom: 47px;
      }

      @include big-mobile {
        line-height: 16px;
      }

      @include mobile {
        margin-bottom: 17px;
      }
    }
  }

  &--first {
    @include image-set('../img/projects-4');

    @include tablet {
      @include image-set('../img/projects-4-tablet');
    }

    @include small-tablet {
      @include image-set('../img/projects-4-mobile');
    }

    @include mobile {
      @include image-set('../img/projects-4-sm');
    }
  }

  &--second {
    @include image-set('../img/projects-5');

    @include tablet {
      @include image-set('../img/projects-5-tablet');
    }

    @include small-tablet {
      height: 300px;
      @include image-set('../img/projects-5-mobile');
    }

    @include mobile {
      height: 422px;
      @include image-set('../img/projects-5-sm');
    }

    .project__descr {

      @include mobile {
        margin-bottom: 25px;
      }
    }
  }

  &--common {

    .project__descr {
      overflow: hidden;
      height: 66px;
      text-overflow: ellipsis;

      @include tablet {
        height: 44px;
      }

      @include small-tablet {
        height: 60px;
      }

      @include big-mobile {
        font-size: 16px;
      }

      @include mobile {
        height: auto;
      }
    }
  }

  &--overflow {
    .project__descr {
      @include mobile {
        height: 43px;
      }
    }
  }

  &--mid {
    .project__descr {

      @include big-mobile {
        font-size: 16px;
      }
    }
  }

  &__img {
    margin-bottom: 13px;

    @include tablet {
      margin-bottom: 12px;
    }

    @include small-tablet {
      margin-bottom: 15px;
    }

    @include mobile {
      img {
        width: 100%;
      }
    }
  }

  &__descr {
    margin-bottom: 10px;
    line-height: 22px;;
    color: $color-mine;

    @include tablet {
      margin-bottom: 16px;
    }

    @include big-mobile {
      margin-bottom: 9px;
    }
  }

  &__date {
    @include flex-v-center;
    margin-top: auto;
    color: $color-gray;
    fill: currentColor;
  }

  &__icon {
    margin-right: 4px;
    width: 14px;
    height: 14px;
    fill: inherit;
  }

  &__text {
    display: inline-block;
    font-size: 12px;
    line-height: 22px;
    color: inherit;
  }

  &__btn {
    margin-top: auto;

    @include mobile {
      margin-top: 0;
    }
  }
}
