html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: $font-family, sans-serif;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img,
svg {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

textarea {
  resize: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  @include containerMaxWidth(1290px, $desktop-gutter);

  @include small-tablet() {
    @include containerMaxWidth(1290px, $small-tablet-gutter);
  }

  @include small-mobile() {
    @include containerMaxWidth(1290px, $small-mobile-gutter);
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title {
  margin: 0;
  font-family: "FirsNeue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 30.96px;
  color: $color-light;

  @include big-mobile {
    font-size: 18px;
    line-height: 23.22px;;
  }
}

.descr {
  margin: 0;
  font-size: 16px;
  line-height: 18px;

  @include big-mobile {
    font-size: 14px;
    line-height: 18px;
  }
}

.small-text {
  position: absolute;
  top: 7px;
  right: 7px;
  display: inline-block;
  padding: 6px 14px;
  font-size: 14px;
  background-color: $color-mine;
  font-size: 14px;
  color: $color-light;

  @include mid-mobile {
    padding: 4px 14px;
    font-size: 12px;
  }
}

.overflow-hide {
  transform: translateX(-100%);

  @include tablet {
    transform: translateY(100%);
  }
}

.label {
  position: relative;
}

.input {
  $inputElement: &;
  padding: 15px 23px;
  border: 1px solid transparent;
  background-color: $color-mercury;
  font-size: 16px;
  color: $color-mine;
  transition: border-color .3s ease-in-out;

  &::placeholder {
    color: $color-gray;
    transition: color .3s ease-in-out;
  }

  &:focus {
    outline: none;
    border-color: $color-orange;
  }

  @include hover {
    border-color: transparent;
    &::placeholder {
      color: $color-orange;
    }
  }

  &-error-field#{ $inputElement } {
    border: 1px solid $color-red-orange;
  }

  @include mobile {
    font-size: 14px;
  }
}

.input-dark {
  color: $color-light;
}

.textarea {
  height: 150px;
}

.btn {
  padding: 13px 20px;
  font-weight: 700;
  font-size: 16px;
  line-height: 18.75px;
  background-color: $color-orange;
  border: 3px solid transparent;
  color: $color-light;
  transition: background-color .3s ease-in-out, border-color .3s ease-in-out;

  &:focus {
    outline: none;
    border-color: $color-grenadier;
  }

  @include hover {
    border-color: transparent;
    background-color: $color-tuscany;
  }

  &:active {
    border-color: transparent;
    background-color: $color-grenadier;
  }

  @include big-mobile {
    padding: 12px 22px;
    font-size: 14px;
    line-height: 16.41px;
  }

  @include mobile {
    padding: 10px 22px;
  }
}

.btn-secondary {
  background-color: transparent;
  border: 3px solid $color-orange;

  &:focus {
    outline: none;
    background-color: $color-tuscany;
  }

  @include hover {
    background-color: $color-orange;
  }

  &:active {
    border-color: transparent;
    background-color: $color-grenadier;
  }

  @include small-tablet {
    padding: 12px 19px;
  }

  @include big-mobile {
    padding: 9px 18px;
  }
}

.open-btn {
  @include flex-all-center;
  background-color: $color-mine-aplha;
  border-radius: 50px;
  position: absolute;
  top: 50%;
  right: -35px;
  visibility: hidden;
  width: 40px;
  height: 40px;
  color: $color-grenadier;
  fill: currentColor;
  opacity: 0;
  transform: translateY(-50%);
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

  @include tablet {
    top: -35px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__icon {
    width: 30px;
    height: 30px;
    fill: inherit;

    @include tablet {
      transform: rotate(-90deg);
    }
  }
}

.close-btn {
  @include flex-all-center;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50px;
  background-color: #808080;
  fill: $color-light;
  transition: background-color .3s ease-in-out;

  &:focus,
  &:hover {
    outline: none;
    background-color: $color-orange;
  }

  &:active {
    background-color: $color-grenadier;
  }
}

.close-btn__icon {
  width: 9.28px;
  height: 9.19px;
  fill: inherit;
}

.phone-link {
  position: relative;
  z-index: 1;
  display: inline-flex;
  font-family: 'FirsNeue', serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20.64px;
  color: $color-orange;
  fill: currentColor;
  transition: color .3s ease-in-out;

  &__icon {
    width: 20px;
    height: 20px;
  }

  @include ghost(120%, 110%, $color-tuscany);

  &:focus {
    outline: none;

    &::after {
      opacity: 1;
    }

    color: $color-light;
  }

  @include hover {
    &::after {
      opacity: 0;
    }

    color: $color-light;
  }

  &:active {
    &::after {
      opacity: 0;
    }
    color: $color-grenadier;
  }
}

.loader {
  width: 75px;
  height: 75px;
  border-width: 2px;
  border-top: 3px solid transparent;
  border-right: 5px solid $color-grenadier;
  border-bottom: 5px solid $color-grenadier;
  border-left: 5px solid $color-grenadier;
  border-radius: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

[class*="copyrights-pane"] {
  display: none !important;
}

[class*="ground-pane"] {
  filter: grayscale(1);
}
