.contacts {
  margin-bottom: 68px;

  @include tablet {
    margin-bottom: 67px;
  }

  @include mobile {
    margin-bottom: 51px;
  }

  &__title {
    margin-bottom: 22px;
    color: $color-mine;

    @include mobile {
      margin-bottom: 14px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: $grid-gap;

    @include tablet {
      grid-template-columns: repeat(8, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      row-gap: 31px;
    }

    @include mobile {
      grid-template-columns: 1fr;
    }
  }
}

.location {
  position: relative;
  grid-column: span 4;

  @include tablet {
    grid-column: span 5;
  }

  @include small-tablet {
    grid-column: span 6;
    height: 460px;
  }

  @include mobile {
    grid-column: span 1;
    margin-inline: -$small-tablet-gutter;
    height: 472px;
  }

  @include small-mobile {
    margin-inline: -$small-mobile-gutter;
  }

  &__overflow-wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;

    @include tablet {
      display: flex;
    }
  }

  &__map {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-gray;
  }

  &__street {
    position: relative;
    padding: 20.6% 15px 15px 60px;
    width: 48.3%;
    height: 100%;
    background-color: $color-mine-aplha;
    transition: transform .3s ease-in-out;

    @include big-desktop {
      @include flex-all-center;
      flex-direction: column;
      padding: 0;
    }

    @include tablet {
      align-self: flex-end;
      align-items: flex-start;
			padding: 23px 20px 21px 30px;
      width: 100%;
      height: auto;
    }

    @include big-mobile {
      padding: 26px 18px 13px;
    }
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  &__title {
    margin-bottom: 5px;
    font-weight: 400;
    line-height: 22px;

    @include mobile {
      margin-bottom: 8px;
    }
  }

  &__address {
    font-style: normal;
    font-size: 16px;
    line-height: 28px;
    color: $color-silver;

    @include big-mobile {
      font-size: 14px;
      line-height: 18px;
    }
  }

  &__link {
    display: flex;
    margin-top: 3px;

    @include ghost(100%, 105%,  $color-tuscany);

    @include tablet {
      display: inline-flex;
      margin-left: 9px;
    }

    @include big-mobile {
      display: flex;
      padding-left: 0;
      margin-top: 13px;
      margin-left: 0;
    }
  }

  &__icon {
    width: 22px;
    height: 22px;

    @include tablet {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    @include big-mobile {
      position: static;
      top: auto;
      left: auto;
      transform: none;
    }

    @include mobile {
      width: 20px;
      height: 20px;
    }
  }

  &__text {
    font-weight: 400;

    @include tablet {
      display: inline-flex;
      padding-left: 23px;
    }

    @include big-mobile {
      padding-left: 0;
    }

    @include mobile {
      font-size: 14px;
      font-weight: 500;
    }
  }

  .phone-link {

    &::after {
      width: 100%;
    }
  }
}

.application {
  grid-column: span 2;

  @include tablet {
    grid-column: span 3;
  }

  @include small-tablet {
    grid-column: span 4;
  }

  @include mobile {
    grid-column: span 1;
  }

  &__title {
    margin-top: -6px;
    margin-bottom: 5px;
    font-weight: 400;
    line-height: 22px;
    color: $color-mine;

    @include tablet {
      margin-top: -8px;
    }

    @include mobile {
      margin-bottom: 8px;
    }
  }

  &__descr {
    margin-bottom: 14px;
    line-height: 22px;
    color: $color-gray;

    @include mobile {
      line-height: 18px;
    }
  }

  &__form {
    @include flex;
    flex-direction: column;
  }

  &__label {
    @include mb(20px);
  }

  &__input {
    width: 100%;

    @include mobile {
      padding: 11px 23px;
    }
  }

  &__textaera {
    margin-bottom: 17px;

    @include tablet {
      margin-bottom: 20px;
    }
  }

	&__btn {
		align-self: start;

    @include mobile {
      padding: 9px 19px;
    }
	}

}


.overflow-hide {
  .open-btn {
    visibility: visible;
    opacity: 1;
  }
}
