.hero {
  margin-bottom: 68px;

  @include mobile {
    margin-bottom: 50px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: $grid-gap;

    @include mobile {
      grid-template-columns: 1fr;
    }
  }
}

.announcement {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 60px;
  @include image-set("../img/hero-photo-1");
  @include bg-cover;

  @include tablet {
    padding: 30px;
    @include image-set("../img/hero-photo-1-tablet");
  }

  @include mid-tablet {
    padding: 28px;
  }

  @include small-tablet {
    @include image-set("../img/hero-photo-1-mobile");
  }

  @include mobile {
    margin-bottom: 35px;
    height: 422px;
    @include image-set("../img/hero-photo-1-sm");
  }

  &__sticker {
    top: 15px;
    right: 15px;
    padding: 7px 14px;

    @include mid-mobile {
      top: 6px;
      right: 5px;
      padding: 4px 7px;
    }
  }

  &__descr {
    margin-bottom: 3px;
    font-size: 16px;
    line-height: 18.75px;
    color: $color-light;

    @include tablet {
      margin-bottom: 9px;
    }

    @include mid-tablet {
      margin-bottom: 15px;
    }

    @include big-mobile {
      font-size: 14px;
      line-height: 16.41px;
    }

    @include mobile {
      margin-bottom: 7px;
    }
  }

  &__title,
  &__time {
    margin: 0;
    font-family: "FirsNeue", sans-serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 37.44px;
    color: $color-light;

    @include tablet {
      font-size: 34px;
      line-height: 34px;
    }

    @include mid-tablet {
      font-size: 34px;
      line-height: 30.6px;
    }

    @include big-mobile {
      font-size: 24px;
      line-height: 26.4px;
    }
  }

  &__time {
    margin-bottom: 19px;

    @include tablet {
      margin-bottom: 22px;
    }

    @include mobile {
      margin-bottom: 13px;
    }
  }
}

.examples {

  &__title {
    margin: 0;
    margin-bottom: 18px;
    line-height: 24.96px;
    color: $color-mine;

    @include tablet {
      margin-bottom: 24px;
    }

    @include mid-tablet {
      margin-bottom: 17px;
      font-size: 22px;
      line-height: 22.88px;
    }

    @include big-mobile {
      font-size: 18px;
      line-height: 18.72px;
    }

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $grid-gap;

    @include mobile {
      grid-template-columns: minmax(auto, 1fr) minmax(124px, 1fr) minmax(124px, 1fr);
      grid-template-rows: minmax(auto, 120px);
    }

    @include mid-mobile {
      grid-template-columns: 2fr;
      grid-template-rows: 1fr 1fr;
    }
  }

  &__photo {
    line-height: 0;
  }

  &__photo--big {
    grid-column: span 2;
    grid-row: span 2;

    img {
      height: 100%;
    }

    @include mid-mobile {
      justify-self: center;;
    }
  }
}
