.footer {
  background-color: $color-mine;

  &__container {
    padding-top: 25px;
    padding-bottom: 16px;

    @include tablet {
      padding-top: 27px;
    }

    @include mobile {
      padding-top: 31px;
      padding-bottom: 0px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr) 0.8fr repeat(5, 1fr) ;

    @include tablet {
      grid-template-columns: repeat(2, 1fr) 1.8fr 1fr repeat(5, 1fr);
    }

    @include mid-tablet {
      grid-template-columns: minmax(263px, 1fr) minmax(161px, 1fr);
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: 27px;
    }
  }

  &__logo {
    grid-column: span 3;

    .logo {
      display: inline-block;
      margin-bottom: 5px;
    }

    @include mid-tablet {
      grid-column: auto;
    }

    @include mobile {
      justify-self: stretch;
      text-align: center;
      margin-inline: -$small-tablet-gutter;
      padding: 14px 0;
      background-color: $color-tundora;
    }

    @include small-mobile {
      margin-inline: -$small-mobile-gutter;
    }

    @include mobile {
      padding-bottom: 9px;
    }
  }

  &__copy {
    display: block;
    font-family: 'FirsNeue';
    font-size: 14px;
    color: $color-mercury;

    @include small-mobile {
      font-size: 10px;
    }
  }

  &__quote {
    grid-column: span 4;
    font-size: 16px;
    line-height: 22px;
    color: $color-silver;

    @include mid-tablet {
      display: none;
    }
  }

  &__social {
    grid-column: span 2;
    text-align: end;

    @include mid-tablet {
      grid-column: auto;
      justify-self: end;
    }

    @include mobile {
      grid-row: 1;
      justify-self: center;
    }

    .social {
      display: inline-flex;
      justify-content: space-around;
      align-items: center;
      gap: 20px;
      margin-right: 40px;

      &__link {
        display: inline-block;
        color: $color-silver;
        fill: currentColor;

        &:focus {
          outline: none;
          color: $color-tuscany;
        }

        @include hover {
          color: $color-orange;
        }

        &:active {
          color: $color-grenadier;
        }
      }

      &__icon {
        width: 30px;
        height: 30px;
        fill: inherit;
        transition: color .3s ease-in-out;
      }
    }
  }

  &__more {
    display: block;
    font-size: 16px;
    margin-bottom: 8px;
    line-height: 22px;
    color: $color-silver;

    @include tablet {
      margin-bottom: 5px;
    }

    @include mobile {
      margin-bottom: 8px;
    }
  }
}

