.about {
  margin-bottom: 68px;
  background-color: $color-mine;

  @include mobile {
    margin-bottom: 50px;
  }

  &__container {
    padding-top: 32px;
    padding-bottom: 67px;

    @include tablet {
      padding-bottom: 78px;
    }

    @include mid-tablet {
      padding-bottom: 44px;
    }

    @include mobile {
      padding-bottom: 60px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;

    @include big-mobile {
      grid-template-columns: 1fr;
    }
  }

  &__info {
    @include small-tablet {
      margin-bottom: 18px;
    }

    @include big-mobile {
      margin-bottom: 22px;
    }
  }

  &__title {
    margin-bottom: 3px;

    @include big-mobile {
      margin-bottom: 7px;
    }

    @include mobile {
      margin-bottom: 5px;
    }
  }

  &__descr {
    line-height: 28px;
    color: $color-silver;

    @include big-mobile {
      line-height: 25px;
    }
  }
}

.subscribe {
  display: grid;
  grid-template-rows: 2.8fr 1fr;
  row-gap: 25px;

  @include tablet {
    grid-template-rows: 2.06fr 1fr;
  }

  @include mid-tablet {
    grid-template-rows: 3.1fr 1fr;;
  }

  @include big-mobile {
    grid-template-rows: 278px 1fr;
  }

  &__bg {
    @include image-set("../img/about-bg");
    @include bg-cover;

    @include tablet {
      @include image-set("../img/about-bg-tablet");
    }

    @include small-tablet {
      @include image-set("../img/about-bg-mobile");
    }

    @include big-mobile {
      @include image-set("../img/about-bg");
    }

    @include small-mobile {
      @include image-set("../img/about-bg-sm");
    }
  }

  &__title {
    margin-bottom: 16px;
    font-weight: 400;
    line-height: 22px;
    color: $color-light;

    @include mid-tablet {
      margin-bottom: 14px;
    }
  }

  &__form {
    @include flex;

    @include big-desktop {
      @include flex-all-sb;
    }

    @include tablet {
      align-items: flex-start;
      flex-direction: column;
    }
  }

  &__label {
    margin-right: 30px;
    width: 100%;

    @include tablet {
      margin-right: 0;
      margin-bottom: 19px;
      width: 342px;
    }

    @include mid-tablet {
      width: 100%;
    }
  }

  &__input {
    padding: 15.5px 23px;
    width: 100%;
    height: 100%;
    background-color: $color-tundora;
    line-height: 18.75px;

    @include big-mobile {
      padding: 11.5px 23px;
      line-height: 16.41px;
    }
  }

  &__btn {

    @include mobile {
      padding: 9px 19px;
    }
  }
}
