@import "./menu";
@import "./search";

.header {
  position: relative;
  margin-bottom: 41px;
  background-color: $color-mine;

  @include tablet  {
    margin-bottom: 35px;
  }

  @include mid-mobile {
    margin-bottom: 30px;
  }

  &__container {
    display: grid;
    grid-template-columns: minmax(190px, auto) 1fr minmax(auto, 54px) minmax(auto, 25px);
    align-items: center;
    padding-top: 24px;
    padding-bottom: 21px;

    @include tablet {
      grid-template-columns: minmax(190px, auto) 1fr minmax(auto, 58px) minmax(auto, 25px);
      padding-top: 26px;
      padding-bottom: 23px;
    }

    @include mid-tablet {
      grid-template-columns: 1fr 1fr;
      row-gap: 22px;
      padding-top: 27px;
      padding-bottom: 0;
    }

    @include mid-mobile {
      padding-top: 25px;
    }
  }

  &__logo {
    margin-right: $adaptive-gutter;

    @include mid-tablet {
      margin-right: 0;
    }
  }

  &__nav {
    margin-right: $adaptive-gutter;

    @include mid-tablet {
      @include flex-all-sb;
      grid-row: 2;
      grid-column: span 2;
			margin-right: 0;
      margin-inline: -$desktop-gutter;
      padding: 12px $desktop-gutter;
      background-color: $color-tundora;
    }

    @include small-tablet {
      margin-inline: -$small-tablet-gutter;
      padding: 12px $small-tablet-gutter;
    }

    @include small-mobile {
      margin-inline: -$small-mobile-gutter;
      padding: 13px $small-mobile-gutter;
    }
  }

  &__search {
    grid-column-start: 4;

    @include mid-tablet {
      grid-column-start: 2;
      justify-self: end;
    }
  }
}


